export default "import React from 'react';\nimport Image from '@splunk/dashboard-visualizations/Image';\nimport { ImageContextProvider } from '@splunk/visualization-context/ImageContext';\nimport ImageRegistry from '@splunk/visualization-context/ImageRegistry';\nimport MockedProvider from '@splunk/visualization-context/MockedProvider';\n\nconst imageRegistry = ImageRegistry.create();\nconst mockedProvider = new MockedProvider();\nimageRegistry.addProvider(mockedProvider);\nmockedProvider.upload('https://splunkudftest.s3-us-west-1.amazonaws.com/buttercup.png');\n\nexport default () => (\n    <ImageContextProvider value={imageRegistry}>\n        <Image\n            options={{\n                src: 'mock://0',\n            }}\n        />\n    </ImageContextProvider>\n);\n";